import Footer from "../Footer";
import Header from "../Header";
import "./SignUp.css";
import React, { useEffect, useState } from "react";

export default function SignOut() {
  const [email, setEmail] = useState("");
  const [nickname, setNickname] = useState("");
  const [id, setId] = useState("");
  const [text, setText] = useState("");
  const word = " .";

  const token = window.sessionStorage.getItem("Authorization");

  useEffect(() => {
    //    API 
    const fetchData = async () => {
      fetch("https://ashortwalk.store/api/users", {
        method: "GET",
        headers: {
          Authorization: token,
        },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("   .");
          }
          return response.json();
        })
        .then((data) => {
          setEmail(data.email);
          setId(data.id);
        })
        .catch((error) => {});
    };

    fetchData();
  }, []); //          

  const handleInputChange = (e) => {
    setText(e.target.value.trim());
  };

  const handleUserEditSubmit = (e) => {
    if (word != text) {
      alert("[]   .");
      return;
    }

    e.preventDefault(); //     

    fetch(`https://ashortwalk.store/api/users/${id}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: token,
      },
      body: JSON.stringify({ nickname }),
    })
      .then((response) => {
        if (!response.ok) {
          alert("  .");
        }
        return response.json();
      })
      .then((data) => {
        alert(" .");
        window.location.href = "/";
        window.sessionStorage.removeItem("Authorization");
        window.sessionStorage.removeItem("token");
      })
      .catch((error) => {});
  };

  return (
    <div>
      <Header />
      <div className="container">
        <div className="form-container">
          <h1> </h1>
          <div className="form-row">
            <label>E-mail</label>
            <input type="text" id="email" value={email} disabled></input>
          </div>
          <br />
               .
          <br /> [ .]
          <div className="form-row">
            <input type="text" id="text" onChange={handleInputChange}></input>
          </div>
          <button className="special-button" onClick={handleUserEditSubmit}>
            
          </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
