import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import Header from "../Header";
import Footer from "../Footer";
import PostImgContent from "./PostImgContent";
import PostCommentContent from "./PostCommentContent";

import "./Post.css";
const Post = () => {
  const { id } = useParams(); // URL ID 
  const [data, setData] = useState(null); // API  
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); //  
  const [user, setUser] = useState("");
  const authorization = window.sessionStorage.getItem("Authorization");

  useEffect(() => {
    const fetchPost = async () => {
      try {
        setLoading(true); //  
        const result = await axios.get(
          `https://ashortwalk.store/api/posts/${id}`
        );
        setData(result.data); //  
      } catch (err) {
        setError(err); //  
      } finally {
        setLoading(false); //  
      }
    };
    const findUser = async () => {
      const response = await axios.get(`https://ashortwalk.store/api/users`, {
        headers: { authorization },
      });
      setUser(response.data);
    };
    findUser();

    fetchPost();
  }, [id]);

  if (loading) return <p>Loading...</p>; //   
  if (error) return <p>Error: {error.message}</p>; //  

  return (
    <div className="post">
      <Header />
      <div className="main-content">
        <PostImgContent data={data} user={user} />
        <PostCommentContent user={user} />
      </div>

      <Footer />
    </div>
  );
};

export default Post;
