import "./FeedPage.css";
import Header from "../Header";
import Footer from "../Footer";
import axios from "axios";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import Pagination from "../posts/Pagination";
import Chat from "./Chat";

export default function FeedPage() {
  const authorization = window.sessionStorage.getItem("Authorization");
  const { groupId } = useParams();
  const [feedlist, setFeedlist] = useState([]); // 
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [myGroup, setMyGroup] = useState({});
  const [mission, setMission] = useState({});
  const [user, setUser] = useState({});
  const [totalMember, setTotalMember] = useState(0);
  const [totalComplete, setTotalCompelete] = useState(0);
  const [isComplete, setIsComplete] = useState(false);

  useEffect(() => {
    try {
      const findTotalMemberCount = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/members/count`,
          { headers: { authorization } }
        );
        setTotalMember(response.data);
      };
      const findCompleteCount = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/completes`,
          { headers: { authorization } }
        );
        setTotalCompelete(response.data);
      };
      const findUser = async () => {
        const response = await axios.get(`https://ashortwalk.store/api/users`, {
          headers: { authorization },
        });
        setUser(response.data);
      };
      const findFeed = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/feeds?page=${currentPage}`
        );
        setFeedlist(response.data);
      };
      const findMission = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/missions`,
          { headers: { authorization } }
        );
        setMission(response.data);
      };
      const countFeeds = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/feeds/count`
        );
        setTotalPages(response.data);
      };
      const findMyGroup = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}`
        );

        setMyGroup(response.data);
      };
      const isComplete = async () => {
        const response = await axios.get(
          `https://ashortwalk.store/api/groups/${groupId}/completes/check`,
          { headers: { authorization } }
        );
        if (response.data) {
          console.log(response.data);
          setIsComplete(true);
        }
      };

      findFeed();
      countFeeds();
      findMyGroup();
      findMission();
      findUser();
      findTotalMemberCount();
      findCompleteCount();
      isComplete();
    } catch (err) {}
  }, []);

  // const [feeds, setFeeds] = useState([]); // 
  const [content, setContent] = useState(""); // 

  const [editingContentId, setEditingContentId] = useState(null); //    ID
  const [editingContent, setEditingContent] = useState(""); //    

  async function complete() {
    try {
      const response = await axios.post(
        `https://ashortwalk.store/api/groups/${groupId}/completes`,
        {},
        { headers: { authorization } }
      );

      if (response.status == 200 || response.status == 201) {
        setIsComplete(true);
        alert("  .");
        window.location.reload();
      }
    } catch (err) {
      alert("  .");
    }
  }

  async function getPoint() {
    try {
      const response = await axios.delete(
        `https://ashortwalk.store/api/groups/${groupId}/missions`,
        { headers: { authorization } }
      );

      if (response.status == 200 || response.status == 201) {
        setIsComplete(true);
        setMission({});
        alert("  .");
        window.location.reload();
      }
    } catch (err) {
      alert("  .");
    }

    setIsComplete(true);
  }

  // content 
  const handleSubmin = (e) => {
    e.preventDefault();
    if (!content) {
      alert("  ");
      return;
    }
    try {
      const feedContentWrite = async () => {
        const response = await axios.post(
          `https://ashortwalk.store/api/groups/${groupId}/feeds`,
          { content },
          {
            headers: {
              Authorization: authorization,
            },
          }
        );
        if (200 <= response.status < 300) {
          setContent("");
          window.location.reload();
        }
      };
      feedContentWrite();
    } catch (e) {}
  };

  //   
  const startEditing = (id, currentContent) => {
    setEditingContentId(id);
    setEditingContent(currentContent);
  };

  //   
  const saveEditing = async (id) => {
    try {
      const response = await axios.patch(
        `https://ashortwalk.store/api/groups/${groupId}/feeds/${id}`,
        { content: editingContent },
        {
          headers: {
            Authorization: authorization,
          },
        }
      );

      setFeedlist(
        feedlist.map((feed) =>
          feed.id === id ? { ...feed, content: response.data.content } : feed
        )
      );
      setEditingContentId(null); //   
    } catch (error) {}
  };

  //   
  const cancelEditing = () => {
    setEditingContentId(null);
    setEditingContent("");
  };

  //======================================================================================
  return (
    <div className="FeedPage">
      <Header></Header>

      <div className="FPmainContent">
        {/*  */}
        <div className="FeedContent">
          <div className="FeedInfo">
            <div className="FeedInfoText">
              <h2>{myGroup.groupName}</h2>
              <p>{myGroup.description}</p>
              <p>{myGroup.leaderNickname}</p>
              <p>{myGroup.point}  </p>
              <div className="mission-box">
                <h3>{mission.title}</h3>
                <p>{mission.content}</p>
                {!mission && (
                  <>
                    <p className="mission-notice">
                          .
                    </p>
                    <p className="mission-notice">
                          .
                    </p>
                  </>
                )}
                <div className="mission-inner-box">
                  {myGroup.leaderUserId == user.id ? (
                    <div className="mission-button-box">
                      {mission ? (
                        <>
                          {totalComplete !== totalMember ? (
                            totalComplete == 0 && (
                              <button
                                className="mission-edit-button"
                                onClick={(e) => {
                                  e.preventDefault();
                                  window.location.href = `/groups/${groupId}/missions/${mission.id}/edit`;
                                }}
                              >
                                 
                              </button>
                            )
                          ) : (
                            <button
                              className="mission-totally-compelete-button"
                              onClick={(e) => {
                                e.preventDefault();
                                getPoint();
                              }}
                            >
                               
                            </button>
                          )}
                        </>
                      ) : (
                        <button
                          className="mission-create-button"
                          onClick={(e) => {
                            e.preventDefault();
                            window.location.href = `/groups/${groupId}/missions/create`;
                          }}
                        >
                           
                        </button>
                      )}
                    </div>
                  ) : (
                    <div className="mission-button-box"></div>
                  )}
                  {isComplete ? (
                    <></>
                  ) : (
                    <>
                      {mission && (
                        <button
                          className="mission-complete-button"
                          onClick={(e) => {
                            e.preventDefault();
                            complete();
                          }}
                        >
                           
                        </button>
                      )}
                    </>
                  )}
                  {mission && (
                    <p>
                      ({totalComplete}/{totalMember})
                    </p>
                  )}
                </div>
              </div>
            </div>
            <form onSubmit={handleSubmin}>
              <input
                onChange={(e) => {
                  e.preventDefault();
                  setContent(e.target.value);
                }}
                type="text"
              />
              <button type="submit"> </button>
            </form>
          </div>

          <div className="FeedList">
            {feedlist.map((feed) => {
              return (
                <div key={feed.id} className="FeedListContent">
                  <div className="FeedChange">
                    {/*  */}
                    {editingContentId === feed.id ? (
                      <div className="CFeedinput">
                        <input
                          value={editingContent}
                          onChange={(e) => setEditingContent(e.target.value)}
                        />
                        <button onClick={() => saveEditing(feed.id)}>
                          
                        </button>
                        <button onClick={cancelEditing}></button>
                      </div>
                    ) : (
                      <div className="CFeeddetail">
                        <div className="feeddetailBox">
                          <p>{feed.content}</p>
                          <p>{feed.nickname}</p>
                          <p>{feed.createdAt.split("T")[0]}</p>
                        </div>

                        <div className="FeedButton">
                          {/*  */}
                          {editingContentId !== feed.id && (
                            <button
                              onClick={() =>
                                startEditing(feed.id, feed.content)
                              }
                            >
                              
                            </button>
                          )}
                          {/*  */}
                          <button
                            onClick={async () => {
                              if (window.confirm(" ?")) {
                                try {
                                  await axios.delete(
                                    `https://ashortwalk.store/api/groups/${groupId}/feeds/${feed.id}`,
                                    {
                                      headers: {
                                        Authorization: authorization,
                                      },
                                    }
                                  );
                                  setFeedlist(
                                    feedlist.filter(
                                      (feeds) => feeds.id !== feed.id
                                    )
                                  );
                                } catch (e) {}
                              }
                            }}
                          >
                            
                          </button>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
          <Pagination
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
            totalPages={totalPages}
          ></Pagination>
        </div>

        {/*  */}
        <div className="FeedChating">
          <Chat myGroup={myGroup} />
        </div>
      </div>

      <Footer></Footer>
    </div>
  );
}
