import Footer from "../Footer";
import Header from "../Header";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import "./Mission.css";

export default function MissionEdit() {
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const token = window.sessionStorage.getItem("Authorization");
  const { groupId, missionId } = useParams();

  const handleInputChange = (e) => {
    if ("title" === e.target.id) {
      setTitle(e.target.value.trim());
    }
    if ("content" === e.target.id) {
      setContent(e.target.value.trim());
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault(); //     
    await fetch(
      `https://ashortwalk.store/api/groups/${groupId}/missions/${missionId}`,
      {
        method: "PATCH", // POST 
        headers: {
          "Content-Type": "application/json",
          Authorization: token,
        },
        body: JSON.stringify({ title, content }),
      }
    )
      .then((response) => {
        if (200 <= response.status < 300) {
          alert("  .");
          window.history.back();
        } else {
          alert("  .");
        }
        return response.json();
      })
      .then((data) => {})
      .catch((error) => {});
  };

  useEffect(() => {
    const findMission = async () => {
      const response = await axios.get(
        `https://ashortwalk.store/api/groups/${groupId}/missions`,
        { headers: { authorization: token } }
      );
      setTitle(response.data.title);
      setContent(response.data.content);
    };
    findMission();
  }, []);

  return (
    <div>
      <Header />
      <div className="container">
        <div className="form-container">
          <h1> </h1>
          <div className="form-row">
            <label></label>
            <input
              value={title}
              type="text"
              id="title"
              className="input2"
              onChange={handleInputChange}
            ></input>
          </div>
          <div className="form-row">
            <label></label>
            <input
              value={content}
              type="text"
              id="content"
              className="textarea2"
              onChange={handleInputChange}
            />
          </div>
          <button onClick={handleSubmit}></button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
